body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.34;
  font-family: "PT Mono", "Courier New", monospace; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

input,
textarea,
select,
button {
  font: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono-Regular.woff") format("woff"), url("../fonts/PTMono-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Regular.woff") format("woff"), url("../fonts/RobotoCondensed-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff"), url("../fonts/RobotoCondensed-Bold.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

.container {
  max-width: 1440px;
  min-width: 1024px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .container {
      max-width: 1023px;
      min-width: 768px; } }
  @media (max-width: 767px) {
    .container {
      max-width: 767px;
      min-width: 320px; } }

.header {
  height: 881px;
  background-color: #1b2d37;
  background: url("../img/promo-bg.png") no-repeat right center, url("../img/background.jpg") repeat-x; }
  @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .header {
      background: url("../img/promo-bg@2x.png") no-repeat right center, url("../img/background@2x.jpg") repeat-x;
      background-size: 1440px 825px;
      background-position: center top; } }
  @media (max-width: 1023px) {
    .header {
      height: 688px; } }

.header__wrapper {
  padding-top: 33px;
  padding-left: 10px;
  padding-right: 10px; }
  @media (max-width: 1023px) {
    .header__wrapper {
      padding-top: 22px;
      padding-left: 25px;
      padding-right: 0px; } }

.main-nav {
  display: flex;
  padding-left: 2px;
  padding-right: 6px; }

.main-nav__logo {
  margin-right: auto; }
  .main-nav__logo a {
    display: flex;
    align-items: center; }

.main-nav__logo-icon {
  margin-right: 16px; }

.main-nav__slogan {
  margin-top: 2px; }
  @media (max-width: 767px) {
    .main-nav__slogan {
      display: none; } }

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  min-width: 660px; }
  @media (max-width: 1023px) {
    .main-nav__list {
      min-width: auto; } }

.main-nav__item {
  margin-right: 88px;
  cursor: pointer;
  display: flex;
  align-items: center; }
  .main-nav__item a {
    color: #ffffff;
    display: flex;
    align-items: center; }
  .main-nav__item:nth-child(2n) {
    margin-right: 50px; }
  .main-nav__item:last-child {
    margin: 0; }
    .main-nav__item:last-child a {
      font-family: "Roboto Condensed", "Helvetica", sans-serif;
      font-size: 14px;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      border-radius: 5px;
      border: 1px solid #ffffff;
      padding: 12px 27px 13px 26px; }
    @media (max-width: 1023px) {
      .main-nav__item:last-child {
        display: none; } }
  @media (max-width: 1023px) {
    .main-nav__item {
      margin-right: 15px;
      height: 49px; }
      .main-nav__item:nth-child(2n) {
        margin-right: 0; }
      .main-nav__item span {
        display: none; } }

.main-nav__icon {
  margin-right: 20px;
  margin-top: -1px; }
  .main-nav__icon--phone {
    margin-right: 16px; }
  @media (max-width: 1023px) {
    .main-nav__icon {
      width: 25px;
      height: 19px; }
      .main-nav__icon--phone {
        width: 22.5px;
        height: 22.6px; } }

.promo {
  padding-top: 175px; }
  @media (max-width: 1023px) {
    .promo {
      padding-top: 75px;
      padding-left: 65px; } }
  .promo h1 {
    font-family: "Roboto Condensed", "Helvetica", sans-serif;
    font-size: 80px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    line-height: 94px;
    color: #ffffff;
    margin-bottom: 27px; }
    @media (max-width: 1023px) {
      .promo h1 {
        font-size: 60px;
        line-height: 70px;
        letter-spacing: 0.07em;
        max-width: 44%;
        margin-bottom: 29px; } }
  .promo a {
    display: inline-block;
    color: #ffffff;
    font-size: 14px;
    line-height: 16px; }
  .promo > a {
    transform: rotate(-90deg) translate(-128px, -35px);
    opacity: 0.5; }
    @media (max-width: 1023px) {
      .promo > a {
        display: none; } }

.promo__info {
  padding-left: 96px;
  max-width: 720px; }
  @media (max-width: 1023px) {
    .promo__info {
      max-width: 70%;
      padding-left: 52px; } }
  .promo__info p {
    color: #ffffff;
    font-size: 22px;
    position: relative;
    line-height: 34.5px;
    margin: 0;
    margin-bottom: 44px; }
    .promo__info p::before {
      content: "";
      width: 66px;
      height: 2px;
      background-color: #ffffff;
      position: absolute;
      top: 17px;
      left: -93px; }
    @media (max-width: 1023px) {
      .promo__info p {
        font-size: 18px;
        line-height: 28px;
        margin-bottom: 54px; }
        .promo__info p::before {
          width: 33px;
          top: 15px;
          left: -48px; } }
  .promo__info a {
    font-family: "Roboto Condensed", "Helvetica", sans-serif;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    border-radius: 5px;
    font-size: 16px;
    background-color: #00b2ff;
    padding: 19px 45px 20px 45px; }
    @media (max-width: 1023px) {
      .promo__info a {
        padding: 19px 31px 20px 31px; } }
